.dark-screen {
  background: #2f303a;
  color: #fdfdfd;
}

.app-container {
  width: 90%;
  padding-top: 50px;
  padding-bottom: 50px;
  align-content: center;
  margin-right: auto;
  margin-left: auto;
}

/*tabs in profile*/
.profiletabbuttons {
  padding-left: 5rem;
  padding-top: 3rem;
}
.nodisplay {
  display: none;
}
.yesdisplay {
  background: transparent;
}

/* Navbar CSS */
.navbar {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.nav-link {
  font-size: 18px;
  line-height: 22px;
  text-align: center;
}

.navbar-brand {
  font-size: 30px;
  line-height: 35px;
  font-weight: bold;
}
.navbar-brand p {
  font-size: 15px;
  line-height: 18px;
  font-weight: normal;
}

.navbar-brand:hover {
  cursor: pointer;
}

.profile {
  height: 86px;
  width: 76px;
}
.dark-screen .navbar-toggler-icon {
  background-image: url("./assets/images/ham-dark.svg");
}

.light-screen .navbar-toggler-icon {
  background-image: url("./assets/images/ham-light.svg");
}
.light-screen .nav-link {
  color: #2f303a;
}

.light-screen .nav-link:hover {
  background: #2f303a;
  color: #fdfdfd;
}

.light-screen .navbar-brand {
  color: #2f303a;
}

.light-screen .navbar-brand:hover {
  color: #2f303a;
}

.light-screen .toggler {
  background-color: #2f303a;
}

.light-screen .active {
  background: #2f303a;
  color: #fdfdfd;
}

.dark-screen .nav-link {
  color: #fdfdfd;
}

.dark-screen .nav-link:hover {
  background: white;
  color: black;
}

.dark-screen .navbar-brand {
  color: #fdfdfd;
}

.dark-screen .navbar-brand:hover {
  color: #fdfdfd;
}

.dark-screen .toggler {
  background-color: "white";
}

.dark-screen .active {
  background: white;
  color: black;
}

/* Navbar CSS Ends */

/*Form CSS */
.patientForm {
  text-align: center;
  width: 415px;
  background: #ffffff;
}

.patientForm h1 {
  font-size: 32px;
  font-weight: bold;
  color: #000000;
}

.patientForm p {
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}

.patientForm input {
  border: none;
  background-color: #d3d9e0;
  border-radius: 0;
  border-bottom: 3px solid #f59494;
}
.patientForm select {
  border: none;
  background-color: #d3d9e0;
  border-radius: 0;
  border-bottom: 3px solid #f59494;
}

.patientForm .button {
  width: 100%;
  height: 35px;
  background: #f59494;
  border: none;
  font-size: 18px;
  font-weight: bold;
}

@media only screen and (max-width: 415px) {
  .patientForm {
    width: 95%;
  }
}
/*Form CSS Ends*/

/* DESCRIPTION */

.desc img {
  width: 584px;
  margin-bottom: -150px;
}

.desc p {
  font-size: 20px;
  line-height: 18px;
  color: #fdfdfd;
}
/* DESCRIPTION ENDS*/

/* Home Screen CSS */

.how-work h1 {
  font-weight: bold;
  font-size: 28px;
  line-height: 33px;
}

/* Home Screen CSS ends */

/*TABLE CSS */
.patientTable {
  font-size: 20px;
  line-height: 23px;
  color: #2f303a;
}
.patientTable tr {
  cursor: pointer;
}
.patientTable th {
  padding: 3px 15px 3px 15px;
  background: #c4c4c8;
  text-align: center;
}
.patientTable td {
  padding: 3px 15px 3px 15px;
  color: rgba(0, 0, 0, 0.6);
  text-align: center;
}

/* TABLE CSS ENDS */

/* FOOTER CSS */
.footer {
  background: #302b2b;
  overflow: hidden;
  padding: 30px;
}

.footer .row {
  width: 95%;
  align-content: center;
  margin-right: auto;
  margin-left: auto;
  color: #fdfdfd;
}

.footer h1 {
  font-weight: bold;
  font-size: 28px;
  line-height: 33px;
}
.footer h3 {
  font-size: 20px;
  line-height: 23px;
}

.footer ul li {
  display: inline;
  margin-right: 40px;
}

.footer ul {
  margin-left: 0;
  margin-top: 2rem;
  padding: 0;
}

.copyright {
  color: #ffffff;
}

.social-icon {
  height: 35px;
}

/* FOOTER CSS ENDS */

/* Active Rides Page */

.activeRidesList {
  font-size: 16px;
  line-height: 20px;
}

.activeRidesList tr {
  cursor: pointer;
}

.activeRidesList td {
  padding: 4px 2px 4px 2px;
  color: #265216;
}
.activeRidesList {
  min-width: 300px;
}

.alternate-table-color tr:nth-child(even) {
  background-color: #edf2f7;
}

.alternate-table-color tr:nth-child(odd) {
  background-color: #fdfdfd;
}

.rides h3 {
  font-weight: bold;
  font-size: 40px;
  line-height: 47px;
  text-align: center;
}

.ride-header {
  color: rgba(0, 0, 0, 0.6) !important;
}
.ride-ans {
  text-align: right;
}

/* Active Rides Page CSS ENDS */

/* Profile Page CSS */
.driverL {
  max-height: 200px;
}

/* Hospital Details CSS */

.hosp-image {
  min-width: 230px;
}

.hospitalDetails h4 {
  font-weight: bold;
  font-size: 30px;
  line-height: 35px;
}

.hospitalDetails p {
  font-size: 16px;
  line-height: 19px;
}

.hospitalCode td {
  font-size: 23px;
  line-height: 27px;
}
.hospitalSubDetails {
  font-size: 18px;
  line-height: 25px;
}

.hospitalDetails .td-head {
  padding-right: 20px;
}
.hospitalDetails span {
  min-width: 20px;
}
.colon {
  padding-right: 20px;
}
.subExpiry {
  color: #e97676;
}
/* Hospital Details CSS ends */

.reg-driver td {
  color: #c4c4c4;
}

.reg-driver {
  min-width: 250px;
}

.hospitalProfile p {
  font-size: 20px;
  line-height: 27px;
  color: #c4c4c4;
}

.past-rides h4 {
  font-weight: bold;
  font-size: 30px;
  line-height: 35px;
}

.past-rides h5 {
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
}

.past-ride-map {
  width: 729px;
  height: 298px;
}

.profileRideDet {
  margin-top: auto;
  margin-bottom: auto;
}

.homeLocation input {
  border: none;
  background-color: #edf2f7;
  border-radius: 0;
  height: 40px;
}

.myLocationBtn {
  width: 100%;
  height: 40px;
  background-color: #767e86;
  border: none;
  color: #ffffff;
  font-size: 18px;
}

.homeLocation label {
  margin-bottom: 0;
}

/*aboutus in register*/
.aboutuscontainer {
  width: 100%;
  color: #000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5rem;
}

.aboutheading {
  text-align: start;
  font-size: clamp(1.5rem, 5vw, 2rem);
  margin-bottom: 3rem;
  padding: 0 2rem;
}
.aboutuscontainer p {
  text-align: start;
  margin-bottom: 1rem;
  font-size: 17px;
  font-weight: 200;
  padding: 0 2rem;
  color: #3b3b3b;
}
.contentwrapper {
  display: grid;
  grid-template-columns: 1.75fr 1fr;
}
.coloum1 {
  display: grid;

  grid-gap: 10px;
}
.coloum2 {
  display: grid;

  grid-gap: 10px;
}
.aImages {
  max-width: 400px;
  margin-top: -8rem;
}
@media screen and (max-width: 768px) {
  .contentwrapper {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 0.75fr;
  }
  .aImages {
    max-width: 300px;
    max-height: 300px;
    margin-top: 0rem;
  }
  .aboutuscontainer {
    padding: 30px;
  }
  .aboutuscontainer p {
    font-size: 15px;
  }
}
