.totalformcss {
  padding: 3rem 6rem;
}
.input {
  text-align: center;
}
.insideformtotal {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
label {
  width: 170px;
  text-align: right;
  padding-right: 30px;
}
.allbuttons {
  text-align: center;
  margin-left: 30rem;
  width: 300px;
}
.allbuttons button {
  background-color: #c7ea46;
  padding: 8px 30px;
  border-radius: 6px;
}
.imagesside {
  display: grid;
  grid-template-rows: 0.6fr 1fr;
}
