.colcolumn1 {
  padding-left: 5rem;
  padding-right: 5rem;
}
.colpheading {
  text-align: start;
  font-weight: 600;
  font-size: clamp(1.5rem, 5vw, 2rem);
  margin-bottom: 2rem;
  margin-top: 5rem;
  padding: 0 2rem;
}
.colWrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 5px;
}
.StatsBox {
  padding: 2rem;
}
.Icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.Title {
  font-size: clamp(1.25rem, 2.5vw, 1.5rem);
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.hoshowcontainer {
  margin-top: 6rem;
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  padding: 0 5rem;
  grid-gap: 20px;
}
@media screen and (max-width: 768px) {
  .hoshowcontainer {
    grid-template-columns: 1fr;
    padding: 0 1px;
  }
}
.workheading {
  text-align: start;
  font-weight: 600;
  font-size: clamp(1.5rem, 5vw, 2rem);
  margin-bottom: 3rem;

  padding: 0 2rem;
}
.hoshowData {
  grid-gap: 30px;
}
.hoscoln1 {
  display: grid;
  grid-template-rows: 0.3fr 1fr;
}
.hoscoln2 {
  display: grid;
}
.hoscoln2 h4 {
  display: grid;
  color: blue;
}
@media screen and (max-width: 768px) {
  .hoscoln2 {
    grid-template-columns: 1fr;
    padding: 0 30px;
  }
  .hosuser1pic {
    position: relative;
    max-width: 300px;
  }
  .hosuser2pic {
    position: relative;
    max-width: 300px;
  }
}
.hoscol1inside {
  padding-left: 2rem;
  line-height: 30px;
  margin-top: -1rem;
}
.hosuser1pic {
  position: relative;
  max-width: 400px;
}
.hosuser2pic {
  position: relative;
  max-width: 400px;
}

@media screen and (max-width: 768px) {
  .hosuser1pic {
    position: relative;
    max-width: 300px;
  }
  .hosuser2pic {
    position: relative;
    max-width: 300px;
  }
  .hoscol1inside {
    margin-top: -3rem;
    line-height: 30px;
    padding-right: 1rem;
    padding-left: 2rem;
  }
}

.Desch {
  color: #3b3b3b;
  font-weight: 300;
}
.Titleh {
  font-size: 22px;
}

.slider {
  margin-top: 5rem;
  overflow: hidden;
  position: relative;
  width: 90%;
}
.images {
  display: flex;
  width: 100%;
}
.images img {
  transition: all 0.15s ease;
  width: 100%;
}
.images input {
  display: none;
}
.dots {
  display: flex;
  justify-content: center;
  margin: 5px;
}
.dots label {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  border: solid #222222 3px;
  cursor: pointer;
  transition: all 0.15s ease;
  margin: 5px;
}
.dots label:hover {
  background: #3b3b3b;
}

.m1 {
  position: relative;
  max-width: 500px;
  transition: 2s;
}
#img1:checked ~ .first {
  margin-left: 0;
}
#img2:checked ~ .first {
  margin-left: -100%;
}
#img3:checked ~ .first {
  margin-left: -200%;
}
#img4:checked ~ .first {
  margin-left: -300%;
}
